import Link from "next/link";
import { useState } from "react";
import { usePathname } from "next/navigation";
import { Button } from "@/components/ui/button";
import {
  User,
  Settings,
  LogOut,
  Menu,
  X,
  LayoutGrid,
  FileText,
  BarChart3,
} from "lucide-react";
import {
  DropdownMenu,
  DropdownMenuContent,
  DropdownMenuItem,
  DropdownMenuSeparator,
  DropdownMenuTrigger,
} from "@/components/ui/dropdown-menu";
import { supabase } from "@/lib/supabaseClient";

interface SidebarNavigationProps {
  userRole: string;
}

const navLinks = [
  {
    href: "/dashboard",
    label: "Dashboard",
    icon: <LayoutGrid className="mr-2 h-5 w-5" />,
  },
  {
    href: "/receipts",
    label: "Receipts",
    icon: <FileText className="mr-2 h-5 w-5" />,
  },
  {
    href: "/insights",
    label: "Insights",
    icon: <BarChart3 className="mr-2 h-5 w-5" />,
  },
];

export default function SidebarNavigation({
  userRole,
}: SidebarNavigationProps) {
  const [isOpen, setIsOpen] = useState(false);
  const pathname = usePathname();

  const isActiveLink = (href: string) => pathname === href;

  return (
    <>
      {/* Mobile sidebar toggle */}
      <div className="md:hidden fixed top-4 left-4 z-50">
        <Button variant="ghost" size="icon" onClick={() => setIsOpen(!isOpen)}>
          {isOpen ? <X className="h-6 w-6" /> : <Menu className="h-6 w-6" />}
        </Button>
      </div>
      {/* Sidebar */}
      <aside
        className={`fixed top-0 left-0 h-full w-64 bg-white border-r border-slate-200 shadow-lg flex flex-col justify-between z-40 transition-transform duration-300
        ${
          isOpen ? "translate-x-0" : "-translate-x-full"
        } md:translate-x-0 md:static md:block`}
      >
        {/* Logo */}
        <div className="flex items-center h-16 px-6 border-b border-slate-100">
          <img
            src="/icon-192x192.png"
            alt="Optifi Logo"
            className="h-8 w-8 mr-2"
          />
          <span className="text-2xl font-bold accent-text">Optifi</span>
        </div>
        {/* Navigation Links */}
        <nav className="flex-1 flex flex-col gap-2 mt-8 px-4">
          {navLinks.map((link) => (
            <Link
              key={link.href}
              href={link.href}
              className={`flex items-center px-4 py-3 rounded-lg text-base font-medium transition-colors duration-200 mb-1
                ${
                  isActiveLink(link.href)
                    ? "bg-slate-100 accent-text"
                    : "text-slate-700 hover:bg-slate-50 hover:text-slate-900"
                }
              `}
              onClick={() => setIsOpen(false)}
            >
              {link.icon}
              {link.label}
            </Link>
          ))}
        </nav>
        {/* User Dropdown at Bottom */}
        <div className="px-4 py-6 border-t border-slate-100">
          <DropdownMenu>
            <DropdownMenuTrigger asChild>
              <Button
                variant="ghost"
                className="w-full flex items-center justify-start"
              >
                <User className="h-6 w-6 mr-2" />
                <span className="font-medium">Account</span>
              </Button>
            </DropdownMenuTrigger>
            <DropdownMenuContent className="w-56" align="start">
              <DropdownMenuItem asChild>
                <Link href="/profile">
                  <span className="flex items-center">
                    <User className="mr-2 h-4 w-4" />
                    Profile
                  </span>
                </Link>
              </DropdownMenuItem>
              <DropdownMenuItem asChild>
                <Link href="/settings">
                  <span className="flex items-center">
                    <Settings className="mr-2 h-4 w-4" />
                    Settings
                  </span>
                </Link>
              </DropdownMenuItem>
              <DropdownMenuSeparator />
              <DropdownMenuItem
                onClick={async () => {
                  await supabase.auth.signOut();
                  window.location.href = "/";
                }}
              >
                <LogOut className="mr-2 h-4 w-4" />
                <span>Log out</span>
              </DropdownMenuItem>
            </DropdownMenuContent>
          </DropdownMenu>
        </div>
      </aside>
      {/* Overlay for mobile */}
      {isOpen && (
        <div
          className="fixed inset-0 z-30 bg-black bg-opacity-30 md:hidden"
          onClick={() => setIsOpen(false)}
        />
      )}
    </>
  );
}
