"use client";

import { useState } from "react";
import Link from "next/link";
import { usePathname } from "next/navigation";
import { Button } from "@/components/ui/button";
import { Menu, X, User, Settings, LogOut } from "lucide-react";
import {
  DropdownMenu,
  DropdownMenuContent,
  DropdownMenuItem,
  DropdownMenuSeparator,
  DropdownMenuTrigger,
} from "@/components/ui/dropdown-menu";
import { supabase } from "@/lib/supabaseClient";

interface InteractiveNavigationProps {
  isAuthenticated: boolean;
  userRole: string;
  links: Array<{ href: string; label: string }>;
}

export default function InteractiveNavigation({
  isAuthenticated,
  userRole,
  links,
}: InteractiveNavigationProps) {
  const [isOpen, setIsOpen] = useState(false);
  const pathname = usePathname();

  const isActiveLink = (href: string) => {
    return pathname === href;
  };

  return (
    <>
      {/* Desktop Navigation */}
      <div className="hidden md:flex items-center space-x-8">
        {links.map((link) => (
          <Link
            key={link.href}
            href={link.href}
            className={`text-sm font-medium transition-colors duration-200 ${
              isActiveLink(link.href)
                ? "accent-text border-b-2 accent-border"
                : "text-slate-600 hover:text-slate-900"
            }`}
          >
            {link.label}
          </Link>
        ))}
      </div>

      {/* Desktop Auth Buttons */}
      {isAuthenticated && (
        <div className="hidden md:flex items-center space-x-4">
          <DropdownMenu>
            <DropdownMenuTrigger asChild>
              <Button
                variant="ghost"
                className="relative h-10 w-10 rounded-full"
              >
                <User className="h-6 w-6" />
              </Button>
            </DropdownMenuTrigger>
            <DropdownMenuContent className="w-56" align="end">
              <DropdownMenuItem>
                <User className="mr-2 h-4 w-4" />
                <span>Profile</span>
              </DropdownMenuItem>
              <DropdownMenuItem>
                <Settings className="mr-2 h-4 w-4" />
                <span>Settings</span>
              </DropdownMenuItem>
              <DropdownMenuSeparator />
              <DropdownMenuItem
                onClick={async () => {
                  await supabase.auth.signOut();
                  window.location.href = "/";
                }}
              >
                <LogOut className="mr-2 h-4 w-4" />
                <span>Log out</span>
              </DropdownMenuItem>
            </DropdownMenuContent>
          </DropdownMenu>
        </div>
      )}

      {/* Mobile menu button */}
      <div className="md:hidden">
        <Button
          variant="ghost"
          size="icon"
          onClick={() => setIsOpen(!isOpen)}
          className="text-slate-600"
        >
          {isOpen ? <X className="h-6 w-6" /> : <Menu className="h-6 w-6" />}
        </Button>
      </div>

      {/* Mobile Navigation */}
      {isOpen && !isAuthenticated && (
        <>
          {/* Overlay */}
          <div
            className="fixed inset-0 z-40 bg-black bg-opacity-30 md:hidden"
            onClick={() => setIsOpen(false)}
          />
          {/* Sidebar */}
          <aside
            className={`fixed top-0 left-0 h-full w-64 bg-white border-r border-slate-200 shadow-lg flex flex-col z-50 transition-transform duration-300
            ${isOpen ? "translate-x-0" : "-translate-x-full"}`}
          >
            {/* Close button */}
            <button
              className="absolute top-4 right-4 text-slate-500 text-2xl focus:outline-none"
              onClick={() => setIsOpen(false)}
              aria-label="Close menu"
            >
              ×
            </button>
            <nav className="flex-1 flex flex-col gap-2 mt-16 px-4">
              {links.map((link) => (
                <Link
                  key={link.href}
                  href={link.href}
                  className={`flex items-center px-4 py-3 rounded-lg text-base font-medium transition-colors duration-200 mb-1
                    ${
                      isActiveLink(link.href)
                        ? "bg-slate-100 accent-text"
                        : "text-slate-700 hover:bg-slate-50 hover:text-slate-900"
                    }
                  `}
                  onClick={() => setIsOpen(false)}
                >
                  {link.label}
                </Link>
              ))}
            </nav>
          </aside>
        </>
      )}
      {/* Mobile Navigation for authenticated users (if any) */}
      {isOpen && isAuthenticated && (
        <div className="md:hidden fixed inset-0 z-50 bg-black bg-opacity-30">
          <div className="mt-8 mx-2 rounded-lg shadow-lg bg-white border border-slate-200 max-h-[60vh] overflow-y-auto p-2">
            {links.map((link) => (
              <Link
                key={link.href}
                href={link.href}
                className={`block px-3 py-2 text-base font-medium transition-colors duration-200 ${
                  isActiveLink(link.href)
                    ? "accent-text bg-slate-50"
                    : "text-slate-600 hover:text-slate-900 hover:bg-slate-50"
                }`}
                onClick={() => setIsOpen(false)}
              >
                {link.label}
              </Link>
            ))}
          </div>
        </div>
      )}
    </>
  );
}
